<style lang="sass" rel="scss">

    $music-width: 563px;
    $music-padding: 10px;
    $toggle-width: 22px;
    $music-face-size: 90px;
    $music-height : 115px;
    $color-blue: #00bfef;
    $color-gray-light: #e5e9ef;
    $color-gray-bg : #edece9;
    $color-gray: #99a2aa;
    $color-gray-hover: #6d757a;

    #music {
        width: $music-width;
        height: $music-height;
        position: fixed;
        bottom: 30px;
        background-color: #fff;
        box-shadow: 3px 1px 6px rgba(0,0,0,.25);
        border-radius: 0 4px 4px 0;
        z-index: 1000;
        transition: left .6s ease;
        display: flex;
        
        a {
            cursor: pointer;
        }

        div {
            box-sizing: border-box;
        }

        button {
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            background-color: transparent;
            outline: none;
            border: none;
            cursor: pointer;
            padding: 0;
        }

        .toggle {
            width: $toggle-width;
            height: 100%;
            border-radius: 0 4px 4px 0;
            background-color: $color-blue;
            background-image: url(data:image/png;base64,);
            z-index: -1;
        }

        .M-main {
            padding: $music-padding;
            width: $music-width - $toggle-width;
            height: 100%;
            flex: 1;
            display: flex;
            flex-direction: column;
            position: relative;
        }

        .M-face {
            width: $music-height - 2 * $music-padding;
            height: $music-height - 2 * $music-padding;
            margin-right: $music-padding;
        }

        .M-warp {
            height: $music-height - 2 * $music-padding;
            display: flex;
        }

        .M-control {
            flex: 1;
            display: flex;
            flex-direction: column;

            span {
                color: $color-gray;
                font-size: 12px;
            }

            .header {
                display: flex;
                justify-content: space-between;
                align-items: center;
                font-size: 14px;
                
                .menu-btn {
                    background-repeat: no-repeat;
                    background-position: center;
                    background-image: url(data:image/png;base64,);
                    background-size: 20px;
                    width: 20px;
                    height: 20px;
                    transition: .4s;
                }

                .menu-click {
                    transform:rotate(-90deg);
                    -ms-transform:rotate(-90deg);
                    -moz-transform:rotate(-90deg);
                    -webkit-transform:rotate(-90deg);
                    -o-transform:rotate(-90deg);
                }
            }

            .center {
                flex: 1;
                display: flex;

                .lg-btn {
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                }

                .sm-btn {
                    width: 20px;
                    height: 20px;
                }

                .M-play-control {
                    flex: 1;
                    display: flex;
                    align-items: center;
                    justify-content: space-around;

                    .play-wait {
                        background-image: url(data:image/png;base64,);
                    }

                    .play-go {
                        background-image: url(data:image/png;base64,);
                    }

                    .prev {
                        background-image: url(data:image/png;base64,);
                    }

                    .next {
                        background-image: url(data:image/png;base64,);
                    }

                    .M-model-list {
                        background-image: url("./list.png");
                    }

                    .M-model-loop {
                        background-image: url("./loop.png");
                    }

                    .M-model-rand {
                        background-image: url("./rand.png");
                    }
                }

                .M-voice-control {
                    display: flex;
                    align-items: center;

                    .M-all-voice {
                        position: relative;
                        display: inline-block;
                        width: 70px;
                        height: 3px;
                        vertical-align: middle;
                        background-color: $color-gray-light;
                        cursor: pointer;
                        margin-left: 5px;
                    }

                    .M-cur-voice {
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 35px;
                        height: 3px;
                        background-color: $color-blue;
                    }

                    .voice-go {
                        background-image: url(data:image/png;base64,);
                    }

                    .voice-no {
                        background-image: url(data:image/png;base64,);
                    }
                }
            }

            .footer {
                text-align: right;
            }
        }

        .M-curBar, .M-allBar {
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            height: 4px;
        }

        .M-curBar {
            width: 0;
            background-color: $color-blue;
        }

        .M-allBar {
            width: 100%;
            z-index: 2;
            cursor: pointer;
        }

        .M-tip {
            position: absolute;
            bottom: 12px;
            width: 50px;
            height: 17px;
            line-height: 17px;
            font-size: 10px;
            text-align: center;
            background-color: #fff;
            border: solid 1px $color-gray-light;
            border-radius: 3px;

            em {
                border-width: 8px;
                border-color: transparent;
                border-top-color: $color-gray-light;
                border-style: dashed dashed solid;
                position: absolute;
                left: 17px;
                top: 15px;
            }

            i {
                border-width: 6px;
                border-color: transparent;
                border-top-color: #fff;
                border-style: dashed dashed solid;
                position: absolute;
                left: -6px;
                top: -8px;
            }
        }

        .M-menu {
            position: absolute;
            left: 0;
            bottom: 100%;
            width: 100%;
            height: 0;
            background-color: #fff;
            box-shadow: 1px 0 1px rgba(0, 0, 0, 0.25);
            border-radius: 0 5px 0 0;
            transition: height .8s;
            overflow: hidden;

            .header {
                height: 33px;
                background-color: $color-blue;
                padding: $music-padding;
                display: flex;
                justify-content: space-between;
                align-items: center;
                font-family: Microsoft YaHei,Arial,Helvetica,sans-serif;

                .list {
                    color: #fff;
                    font-size: 14px;
                    font-weight: 700;
                }

                .close {
                    width: 20px;
                    height: 20px;
                    display: block;
                    border-radius: 50%;
                    background-color: #fff;
                    color: $color-blue;
                    font-size: 20px;
                    text-align: center;
                    line-height: 20px;
                    cursor: pointer;
                }
            }

            .body {
                font-family: -apple-system,"Helvetica Neue",Arial,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","WenQuanYi Micro Hei",sans-serif;

                >div {
                    padding: 5px 10px;
                }

                .title {
                    height: 30px;
                    border-bottom: 1px solid $color-gray-bg;
                    position: relative;
                    display: flex;
                    font-size: 14px;
                    color: #333;

                    .select {
                        left: 15px;
                        top: 8px;
                        z-index: 5;
                    }
                }

                .item {
                    color: $color-gray;
                    background-color: #fff;
                    font-size: 13px;
                    display: flex;
                    height: 30px;
                    line-height: 20px;

                    &:hover {
                        background-color: $color-gray-bg;

                        .hover {
                            display: block;
                        }

                        .name, .select, .right {
                            color: $color-gray-hover;
                        }
                    }

                    .hover {
                        display: none;
                        position: absolute;
                        right: 20px;
                        top: 0;
                        height: 20px;
                    }

                    .select {
                        left: 5px;
                        top: 3px;
                    }
                }

                .left {
                    flex: 1;
                    display: flex;
                    justify-content: space-between;
                    padding-right: 100px;
                    padding-left: 30px;
                    position: relative;

                    .icon {
                        display: block;
                        width: 14px;
                        height: 14px;
                        background-repeat: no-repeat;
                        background-position: center;
                        float: left;
                        margin-right: 8px;
                    }

                    .icon-pause {
                        background-image: url(data:image/gif;base64,R0lGODlhDgAOAJEAAAAAAP///0up5v///yH5BAEAAAMALAAAAAAOAA4AAAIenI+pqyKMHDySybrusw8PrH2dM4omWYbomZIsq7YFADs=);
                    }

                    .icon-play {
                        background-image: url(data:image/gif;base64,);
                    }
                }

                .right {
                    width: 120px;
                }

                .select {
                    display: block;
                    position: absolute;
                    width: 12px;
                    height: 12px;
                    border-radius: 4px;
                    border: 1px solid $color-gray;
                }

                .selected {
                    &:before {
                        content: '';
                        position: absolute;
                        display: block;
                        left: 2px;
                        top: 2px;
                        width: 7px;
                        height: 7px;
                        border-radius: 3px;
                        background-color: $color-blue;
                    }
                }
            }
        }

        .menu-show {
            height: 500px;
        }
    }

    .M-open {
        left: 0;
    }

    .M-close {
        left: -541px;
    }
</style>

<template v-cloak>
    <div id="music" :class="[ open ? 'M-open' : 'M-close' ]">
        <audio ref="audio"></audio>
        <div class="M-main">
            <div class="M-warp">
                <img class="M-face" ref="banner">
                <div class="M-control">
                    <div class="header">
                        <div>
                            <a>{{ now.name }}</a>
                            <a>{{ now.player }}</a>
                        </div>
                        <button :class="['menu-btn', status.menuShow ? 'menu-click' : '']" @click="status.menuShow = !status.menuShow"></button>
                    </div>
                    <div class="center">
                        <div class="M-play-control">
                            <button :class="['sm-btn', 'M-model-' + status.model]" @click="changeModel"></button>
                            <button class="sm-btn prev" @click="next(false)"></button>
                            <button :class="[ 'lg-btn', status.playing ? 'play-go' : 'play-wait']" ref="paused"></button>
                            <button class="sm-btn next" @click="next(true)"></button>
                        </div>
                        <div class="M-voice-control">
                            <button :class="[ 'sm-btn', status.silent ? 'voice-no' : 'voice-go' ]" ref="muted"></button>
                            <span class="M-all-voice" ref="allvoice">
                                <span class="M-cur-voice" ref="curvoice"></span>
                            </span>
                        </div>
                    </div>
                    <div class="footer">
                        <span>{{ time.cur }} / {{ time.all }}</span>
                    </div>
                </div>
            </div>
            <span class="M-allBar" @mouseout="tipShow = false" ref="alltime">
                <span class="M-curBar" ref="curtime"></span>
            </span>
            <div class="M-tip" v-show="tipShow" ref="tip">{{ tipText }}<em><i></i></em></div>
            <div :class="['M-menu', status.menuShow ? 'menu-show' : '']">
                <div class="header">
                    <span class="list">歌曲列表</span>
                    <span class="close" @click="status.menuShow = false">&times;</span>
                </div>
                <div class="body">
                    <div class="title">
                        <a class="select selected"></a>
                        <div class="left">歌曲</div>
                        <div class="right">歌手</div>
                    </div>
                    <div class="item" v-for="item in source">
                        <div class="left">
                            <a :class="['select', item.selected ? 'selected' : '']" @click="musicSelect(item)"></a>
                            <div>
                                <span v-show="item.playing" :class="['icon', status.playing ? 'icon-play' : 'icon-pause']"></span>
                                <a class="name" @click="loadSource(item)">{{ item.name }}</a>
                            </div>
                            <div class="hover">
                                <button @click="loadSource(item)">播</button>
                                <button @click="musicSelect(item)">列</button>
                            </div>
                        </div>
                        <div class="right">
                            {{ item.player }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <button class="toggle" @click="musicToggle"></button>
    </div>
</template>

<script>

    export default {
        props: {
            source : {
                default : null,
                required : true
            }
        },
        data () {
            return {
                open : false,
                timeWidth : 541,
                voiceWidth : 70,
                tipShow : false,
                tipWidth : 25,
                tipText : "",
                init : true,
                status : {
                    playing : false,
                    silent : false,
                    menuShow : false,
                    model : "list"
                },
                time : {
                    cur : "00:00",
                    all : "00:00"
                },
                now : {},
                list : []
            }
        },
        created () {
            let i;
            for (i in this.source) {
                this.list.push(this.source[i].id)
            }
        },
        methods: {
            musicToggle () {
                this.open = !this.open;

                if (this.init) {

                    this.init = false;

                    this.loadSource(this.source[0], false)
                }
            },
            musicSelect (item) {
                if (item.id === this.now.id) return;
                if (item.selected) {
                    this.list.splice(this.list.indexOf(item.id), 1)
                } else {
                    this.list.push(item.id)
                }
                item.selected = !item.selected;
            },
            next (bool) {
                if (this.status.model === 'loop') return;
                let id = this.now.id;
                let length = this.list.length;
                let i, j, now;
                if (this.status.model === 'list') {
                    for (i in this.list) {
                        if (this.list[i] === id)
                            break
                    }
                    now = this.list[bool ? i == length - 1 ? 0 : ++i : i == 0 ? --length : --i];
                } else {
                    now = this.list[Math.floor(Math.random() * length + 1) - 1]
                }
                for (j in this.source) {
                    if (this.source[j].id === now) {
                        this.loadSource(this.source[j]);
                        break
                    }
                }
            },
            loadSource (item, bool = true) {
                if (this.now.playing !== undefined) {
                    this.now.playing = false
                }
                item.playing = true;
                this.now = item;
                this.$refs.audio.setAttribute('src', this.now.src);
                this.$refs.banner.setAttribute('src', this.now.img);
                if (bool) {
                    this.$refs.audio.play();
                    this.status.playing = true;
                }
            },
            changeModel () {
                let ret = "";
                switch (this.status.model) {
                    case "list" :
                        ret = "rand";
                        this.$refs.audio.loop = false;
                        break;
                    case "rand" :
                        ret = "loop";
                        this.$refs.audio.loop = true;
                        break;
                    case "loop" :
                        ret = "list";
                        this.$refs.audio.loop = false;
                        break;
                }
                this.status.model = ret;
            },
            formatSeconds (second) {
                var minute = 0;
                var hour = 0;
                if (second > 60) {
                    minute = parseInt(second / 60);
                    second = (second % 60).toFixed(0);
                    if (minute > 60) {
                        hour = parseInt(minute / 60);
                        minute = parseInt(minute % 60);
                    }
                } else {
                    second = parseInt(second);
                }
                second = second < 10 ? '0' + second : second;
                minute = minute < 10 ? '0' + minute : minute;
                return [hour, minute, second];
            }
        },
        mounted () {
            var vm = this;
            var audio = this.$refs.audio;
            audio.volume = 0.5;

            audio.oncanplay = function () {
                // 缓冲已足够开始时
            };

            audio.oncanplaythrough = function () {
                // 缓冲已完成
            };

            audio.onended = function () {
                // console.log("onended : 音乐播放完毕");
                vm.status.playing = false;
                vm.next(true)
            };

            audio.onloadedmetadata = function () {
                // 数据的分辨率或时间长度已加载完成
                var time = vm.formatSeconds(this.duration);
                vm.time.all = time[1] + ":" + time[2];
            };

            audio.onplay = function () {
                // console.log("onplay : 播放开始");
            };

            audio.onwaiting = function () {
                // console.log("onwaiting : 播放过程中开始缓冲");
            };

            audio.ontimeupdate = function () {
                // 当播放位置改变时
                var time = vm.formatSeconds(this.currentTime);
                vm.$refs.curtime.style.width = vm.timeWidth * this.currentTime / this.duration + "px";
                vm.time.cur = time[1] + ":" + time[2];
            };

            audio.onplaying = function () {
                // console.log('onplaying : 播放中...');
            };

            this.$refs.muted.onclick = function () {
                vm.status.silent = !vm.status.silent;
                audio.muted = !audio.muted;
            };

            this.$refs.paused.onclick = function () {
                vm.status.playing = !vm.status.playing;
                audio.paused ? audio.play() : audio.pause()
            };

//            document.getElementById('loop').onclick = function () {
//                audio.loop = !audio.loop;
//            };

            this.$refs.allvoice.onclick = function (e) {
                audio.volume = e.layerX / vm.voiceWidth;
                vm.$refs.curvoice.style.width = e.layerX + "px";
            };

            this.$refs.alltime.onclick = function (e) {
                vm.$refs.curtime.style.width = e.layerX + "px";
                audio.currentTime = e.layerX / vm.timeWidth * audio.duration;
            };

            this.$refs.alltime.onmousemove = function (e) {
                var time = vm.formatSeconds(audio.duration / vm.timeWidth * e.layerX);
                vm.tipShow = true;
                vm.$refs.tip.style.left = e.layerX - vm.tipWidth + "px";
                vm.tipText = time[1] + ":" + time[2];
            };
        }
    }
</script>


